main {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  margin: 0;
  background-color: white;
  color: #cc212a; }

.wrapper {
  width: 100%;
  max-width: 1200px;
  margin: auto;
  padding: 15px; }

.main-title span {
  display: block;
  text-align: center;
  font-weight: 700; }

.wrapper {
  width: 100%;
  max-width: 1200px;
  margin: auto;
  padding: 15px; }

.footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  text-align: center;
  font-weight: 300;
  border-top: 1px solid #cc212a; }

.footer-letter {
  background-color: #cc212a;
  color: white;
  border-radius: 50%;
  height: 50px;
  width: 50px;
  line-height: 50px;
  text-align: center;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, -50%); }

.footer .wrapper {
  padding-top: 40px; }

/* Tablet/Desktop version */
@media all and (min-width: 768px) {
  .footer-letter {
    position: static;
    transform: none; }
  .footer .wrapper {
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    align-items: center;
    padding: 15px; } }
